@import "../ui/common.scss";

#experience {
  max-width: 100%;
  width: 100% !important;
  .margin-bubble {
    margin-top: 0;
    @media (max-width: $ipadMaxWidth) {
      margin-top: 20px;
    }
  }
  .time-line {
    text-align: center;

    span {
      border-radius: 100%;
      background: #5bc0de;
      border: 9px solid white;
      padding: 18px 8px 9px;
      z-index: 2;
    }

    span:before {
      content: "";
      position: absolute;
      border-left: 5px solid grey;
      left: 50%;
      bottom: 0;
      z-index: -1;
      height: 100%;
      @media (max-width: $maxWidth) {
        left: 50%;
      }
    }
    .from-to {
      padding-top: 20px;
      background-color: transparent;
      font-size: 18px;
      font-weight: bold;
      margin-top: 100px;
      @media (max-width: $ipadMaxWidth) {
        margin-top: 0px;
      }
    }

    .speech-bubble {
      color: white;
      background: #00aabb;
      border-radius: 0.4em;
      @media (max-width: $maxWidth) {
        height: 250px;
      }
      .card-header {
        font-size: 1.2em;
        @media (max-width: $maxWidth) {
          font-size: 18px;
        }
      }
      .card-text {
        font-size: 17px;
        @media (max-width: $maxWidth) {
          font-size: 10px;
        }
        @media (max-width: $ipadMaxWidth) {
          font-size: 13px;
        }
      }
    }

    .speech-bubble:after {
      content: "";
      position: absolute;
      right: -3.8%;
      top: 50%;
      width: 0;
      height: 0;
      border: 1.094em solid transparent;
      border-left-color: #00aabb;
      border-right: 0;
      margin-top: -1.094em;
      @media (max-width: $ipadMaxWidth) {
        right: 43%;
        top: 106%;
        border: 20px solid transparent;
        border-top-color: #00aabb;
        border-bottom: 0;
      }
    }

    .speech-bubble-right {
      color: white;
      position: relative;
      background: #00aabb;
      border-radius: 0.4em;
      height: 300px;
      @media (max-width: $maxWidth) {
        height: 250px;
      }
      .card-header {
        font-size: 1.2em;
        @media (max-width: $maxWidth) {
          font-size: 18px;
        }
      }
      .card-text {
        font-size: 17px;
        @media (max-width: $maxWidth) {
          font-size: 10px;
        }

        @media (max-width: $ipadMaxWidth) {
          font-size: 13px;
        }
      }
    }

    .speech-bubble-right:after {
      content: "";
      position: absolute;
      left: 0;
      top: 50%;
      width: 0;
      height: 0;
      border: 20px solid transparent;
      border-right-color: #00aabb;
      border-left: 0;
      margin-top: -20px;
      margin-left: -18px;
      @media (max-width: $ipadMaxWidth) {
        top: 39%;
        left: 50%;
        bottom: 100%;
        border: 20px solid transparent;
        border-bottom-color: #00aabb;
        border-top: 0;
        margin-top: -115px;
      }
    }
  }
}
