@import "../ui/common.scss";

#home {
  width: 100% !important;
  h1 {
    .name-highlight {
      color: #04c2c9;
    }
    @media (max-width: $maxWidth) {
      font-size: 25px;
    }
  }

  h3 {
    @media (max-width: $maxWidth) {
      font-size: 20px;
    }
  }

  .indroduction {
    width: 100% !important;
    font-size: 1.3rem;
    line-height: 2.5rem;
    overflow-x: hidden;
    padding: 15px;
    @media (max-width: $maxWidth) {
      font-size: 1.1rem;
      line-height: 2rem;
    }
  }
}
